/* DeleteTask.css */
.delete-task-container {
  margin-top: 10px;
}

.delete-btn {
  background-color: #e74c3c;
  color: #fff;
  padding: 8px;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.delete-btn:hover {
  background-color: #c0392b;
}
